*,
*::before,
*::after {
  box-sizing: border-box;
}

body {
  font-family: 'Open Sans', -apple-system, BlinkMacSystemFont, 'Segoe UI',
    Roboto, Oxygen, Ubuntu, Cantarell, sans-serif;
  line-height: 1.5;
  color: #2a2a2a;
}

img {
  display: block;
  max-width: 100%;
  height: auto;
}

.search-form {
  display: flex;
  justify-content: center;
  padding: 8px;
  margin-bottom: 24px;
}

.search-form input{
  width: 300px;
  height: 50px;
  background-color: #173a85;
  border: none;
  font-size: 10pt;
  float: left;
  color: #dfe3e8;
  padding-left: 45px;
  border-radius: 5px;  
}

.search-form .button{
  margin-left: 10px;
  background-color: #173a85;
  border-radius: 5px;
  color: #dfe3e8;
}

.load-more{
  display: block;
  margin: auto;
  padding: 8px;
  background-color: #173a85;
  border-radius: 5px;
  color: #dfe3e8;
}

.gallery{
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(300px, 1fr));
  grid-gap: 20px;
  padding: 0;
  margin: 0;
  max-width: calc(100vw - 80px);
  margin-left: auto;
  margin-right: auto;
  margin-bottom: 30px;
}
.photo-card{
  height: 450px;
  overflow: hidden;
}
.gallery__item{
  text-decoration: none;
  color: #2a2a2a;
}
.gallery__item .gallery__image{
  display: block;
  width: 100%;
  height: 200px;
  object-fit: cover;
}