*, :before, :after {
  box-sizing: border-box;
}

body {
  color: #2a2a2a;
  font-family: Open Sans, -apple-system, BlinkMacSystemFont, Segoe UI, Roboto, Oxygen, Ubuntu, Cantarell, sans-serif;
  line-height: 1.5;
}

img {
  max-width: 100%;
  height: auto;
  display: block;
}

.search-form {
  justify-content: center;
  margin-bottom: 24px;
  padding: 8px;
  display: flex;
}

.search-form input {
  width: 300px;
  height: 50px;
  float: left;
  color: #dfe3e8;
  background-color: #173a85;
  border: none;
  border-radius: 5px;
  padding-left: 45px;
  font-size: 10pt;
}

.search-form .button {
  color: #dfe3e8;
  background-color: #173a85;
  border-radius: 5px;
  margin-left: 10px;
}

.load-more {
  color: #dfe3e8;
  background-color: #173a85;
  border-radius: 5px;
  margin: auto;
  padding: 8px;
  display: block;
}

.gallery {
  grid-gap: 20px;
  max-width: calc(100vw - 80px);
  grid-template-columns: repeat(auto-fill, minmax(300px, 1fr));
  margin: 0 auto 30px;
  padding: 0;
  display: grid;
}

.photo-card {
  height: 450px;
  overflow: hidden;
}

.gallery__item {
  color: #2a2a2a;
  text-decoration: none;
}

.gallery__item .gallery__image {
  width: 100%;
  height: 200px;
  object-fit: cover;
  display: block;
}

